import { KeyboardEvent } from "react";
import { Dispatcher } from "../../../../../redux/dispatcher/dispatcher";
import { Store } from "../../../../../redux/store/store";

export const NoteKeyDown = (e: KeyboardEvent, store: Store, dispatcher: Dispatcher) => {

    const config = store.userEnv.keyConfig.note;
    const keySwitch = store.scoreState.keySwitch;
    // alert(e.key);
    // alert(config.nextElement);

    if (keySwitch.editMode) {
        switch (e.key) {
            case config.decRoot: {
                dispatcher.score.incrementDegreeRoot(false);
            }
                break;
            case config.incRoot: {
                dispatcher.score.incrementDegreeRoot(true);
            }
                break;
            case config.decBeat: {
                dispatcher.score.incrementBeatLen(false);
            }
                break;
            case config.incBeat: {
                dispatcher.score.incrementBeatLen(true);
            }
                break;
        }
    } else if (keySwitch.symbolMode) {
        switch (e.key) {
            case 'ArrowUp': {
                dispatcher.symbol.toNext(false);
            }
                break;
            case 'ArrowDown': {
                dispatcher.symbol.toNext(true);
            }
                break;
            case 'ArrowLeft': {
                dispatcher.symbol.toBase();
            }
                break;
            case 'ArrowRight': {
                dispatcher.symbol.toDerive();
            }
                break;
        }
    } else {
        switch (e.key) {
            // 
            case config.prevElement: {
                dispatcher.score.moveElement(false);
            }
                break;
            case config.nextElement: {
                dispatcher.score.moveElement(true);
            }
                break;
            // 
            case config.prevSection: {
                dispatcher.score.moveSection(false);
            }
                break;
            case config.nextSection: {
                dispatcher.score.moveSection(true);
            }
                break;
            case config.addChord: {
                dispatcher.score.addChordElement();
            }
                break;
            case config.addSection: {
                dispatcher.score.addSectionElement();
            }
                break;
            case config.deleteCur: {
                dispatcher.score.removeCurElement();
            }
                break;
            case '1':
            case '2':
            case '3':
            case '4':
            case '5':
            case '6':
            case '7': {
                dispatcher.score.setDiatonicChord(Number(e.key) - 1);
            }
                break;
        }
    }
}


export const NoteKeySwitch = (e: KeyboardEvent, store: Store, dispatcher: Dispatcher, isPress: boolean) => {

    const config = store.userEnv.keyConfig.note;
    const keySwitch = store.scoreState.keySwitch;
    // alert(e.key);
    // alnfig.nextElement);
    switch (e.key) {
        case config.editMode: {
            keySwitch.editMode = isPress;
            dispatcher.score.setKeySwitch(keySwitch);
        }
            break;
        case config.symbolMode: {
            keySwitch.symbolMode = isPress;
            dispatcher.score.setKeySwitch(keySwitch);
        }
            break;
    }

    if (isPress) {
        NoteKeyDown(e, store, dispatcher);
    }
}